* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.info {
  color: #FFF;
  margin-top: 10px;
  font-style: oblique;
}

.App {
  background-image:url("./assets/normal.jpg");
  background-size: cover;
  background-repeat:no-repeat;
  background-position: center center;
  transition: 0.4s ease-in-out;  
}

.App.hot {
  background-image:url("./assets/hot.jpg");
  transition: 0.4s ease-out; 
}

.App.cold {
  background-image:url("./assets/cold.jpg");
}

.App.rain {
  background-image:url("./assets/rain.jpg");
}

.App.snow {
  background-image:url("./assets/snow.jpeg");
}

.main-area {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75));
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color:  rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #FFF;
  font-size: 102px;
  font-weight: 900;

  text-shadow: rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

@media screen and (min-width: 600px) {
  .search-box {
    width: 60%;
    margin: auto;
  }

  .search-box .search-bar {
    display: block;
    width: 100%;
    margin-bottom: 50px;
  }  
}